$text-color--muted:           hsl(0, 0%, 50%) !default;

$font-size--small:            0.75em !default;

$line-height:                 1.375em !default;

.field-actions {
  margin-top: 26px;
  text-align: right;

  &__help {
    @include field-help-text;

    margin-top: 10px;
    text-align: center;
  }

  &--left {
    text-align: left;
  }

  &--center {
    text-align: center;
  }

  &--jcsb {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &--mt10 {
    margin-top: 10px;
  }

